.header {
  font-size: 0;
  margin: 40px 0px;
  .header__logo {
    display: block;
    font-size: 1.8rem;
    img {
      max-width: 400px; } }

  .header__search {
    font-family: $fontSerif;
    font-size: 3.6rem;
    text-align: right;
    font-style: italic;
    input {
      font-size: 3.6rem;
      max-width: 270px;
      text-align: right;
      padding-right: 10px;
      border: none;
      background: transparent;
      outline: none;
      @include sm {
        max-width: 400px; } }
    button {
      background: no-repeat center url('../img/search.svg');
      width: 27px;
      height: 27px;
      border: none;
      text-indent: -9999px;
      vertical-align: sub; } } }
